.slider-2__item {
    padding-left: vw(54);
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    @media not all and (max-width: $pc) {
        padding-left: 250px;
    }
}

.slider-2__ttl {
    padding-top: vw(300);
    margin-bottom: vw(35);
    font-size: vw(25);
    font-weight: bold;
    @include ls(400);
    @media not all and (max-width: $pc) {
        font-size: 25px;
        padding-top: 38px;
        margin-bottom: 24px;
    }
}
.slider-2__subTtl {
    margin-bottom: vw(130);
    font-size: vw(17);
    font-weight: normal;
    @include ls(400);
    @media not all and (max-width: $pc) {
        font-size: 17px;
        font-weight: bold;
        margin-bottom: 69px;
    }
}
.slider-2__text {
    margin-bottom: vw(65);
    font-size: vw(14);
    font-weight: normal;
    @include ls(200);
    width: vw(405);
    @media not all and (max-width: $pc) {
        font-size: 14px;
        @include lh(14, 24);
        margin-bottom: 44px;
    }
}
.slider-2__point {
    display: none;
    @media not all and (max-width: $pc) {
        width: 891px;
        display: block;
    }
    @media not all and (max-width: 1200px) {
        width: vw(891, 1200);
    }
}
